import * as PropTypes from "prop-types";
import React from "react";
import { graphql } from "gatsby";
import Layout from "../../components/layout";
import { Helmet } from "react-helmet";

class DataTemplate extends React.Component {
  static propTypes = {
    data: PropTypes.shape({
      filmJson: PropTypes.object.isRequired,
    }),
  };
  render() {
    return (
      <Layout>
        <Helmet>
          <title>
            {this.props.data.filmJson.name} | Archives of Tomorrow: Illustrated
            Media Format Guides
          </title>
        </Helmet>
        <div className="grid grid-cols-1">
          <div className="m-4 mb-0 text-center">
            <h1>{this.props.data.filmJson.name}</h1>
          </div>
        </div>
        <div className="w-11/12 grid lg:grid-cols-3 gap-2 m-4 border-2 text-lg border-orange-800">
          {this.props.data.filmJson.aka && (
            <div className="border-r-2 border-b-2 border-orange-800">
              <span className="relative top-0 left-2 text-xs uppercase text-orange-900">
                AKA
              </span>
              <div className="pl-6">{this.props.data.filmJson.aka}</div>
            </div>
          )}
          {this.props.data.filmJson.era && (
            <div className="border-r-2 border-b-2 border-orange-800">
              <span className="relative top-0 left-2 text-xs uppercase text-orange-900">
                Era
              </span>
              <div className="pl-6">{this.props.data.filmJson.era}</div>
            </div>
          )}
          {this.props.data.filmJson.format && (
            <div className="border-r-2 border-b-2 border-orange-800">
              <span className="relative top-0 left-2 text-xs uppercase text-orange-900">
                Format
              </span>
              <div className="pl-6">{this.props.data.filmJson.format}</div>
            </div>
          )}
          {this.props.data.filmJson.format2 && (
            <div className="border-r-2 border-b-2 border-orange-800">
              <span className="relative top-0 left-2 text-xs uppercase text-orange-900">
                Format2
              </span>
              <div className="pl-6">{this.props.data.filmJson.format2}</div>
            </div>
          )}
          {this.props.data.filmJson.developedBy && (
            <div className="border-r-2 border-b-2 border-orange-800">
              <span className="relative top-0 left-2 text-xs uppercase text-orange-900">
                Developed by
              </span>
              <div className="pl-6">{this.props.data.filmJson.developedBy}</div>
            </div>
          )}
          {this.props.data.filmJson.aspectRatio && (
            <div className="border-r-2 border-b-2 border-orange-800">
              <span className="relative top-0 left-2 text-xs uppercase text-orange-900">
                Aspect Ratio
              </span>
              <div className="pl-6">{this.props.data.filmJson.aspectRatio}</div>
            </div>
          )}
          {this.props.data.filmJson.size && (
            <div className="border-r-2 border-b-2 border-orange-800">
              <span className="relative top-0 left-2 text-xs uppercase text-orange-900">
                Size
              </span>
              <div className="pl-6">{this.props.data.filmJson.size}</div>
            </div>
          )}
        </div>
        <div className="grid grid-cols-1 m-4 border-2 border-orange-800">
          <div className="uppercase relative top-0 left-2 text-lg text-orange-900">
            Fun facts
          </div>
          {this.props.data.filmJson.fact1 && (
            <div className="pl-6 pr-10 pt-2 border-b-2 border-orange-800">
              {this.props.data.filmJson.fact1}
            </div>
          )}
          {this.props.data.filmJson.fact2 && (
            <div className="pl-6 pr-10 pt-2 border-b-2 border-orange-800">
              {this.props.data.filmJson.fact2}
            </div>
          )}
          {this.props.data.filmJson.fact3 && (
            <div className="pl-6 pr-10 pt-2 border-b-2 border-orange-800">
              {this.props.data.filmJson.fact3}
            </div>
          )}
        </div>
      </Layout>
    );
  }
}

export default DataTemplate;

export const pageQuery = graphql`
  query ($jsonId: String!) {
    filmJson(jsonId: { eq: $jsonId }) {
      jsonId
      name
      aka
      era
      format
      format2
      developedBy
      aspectRatio
      size
      fact1
      fact2
      fact3
    }
  }
`;
